* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.container {
  border: 1px solid red;
  height: 100vh;
  padding: 20px;
}
.cardlebel div:first-child {
  padding: 20px;
  margin: 20px;
  width: 90%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.cardlebel {
  display: flex;
  margin: 0 10%;
}
.cardlebel div:last-child{
  padding: 20px;
  margin: 20px;
  width: 10%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.total {
  text-align: center;
  margin-top: 30px;
}
.btn button {
  padding: 1px 2px;
  font-size: 18px;
  border: none;
  &:hover {
    background-color: black;
    color: white;
  }
}